import { Injectable } from '@angular/core';
import { Http, Headers, Response } from '@angular/http';
import { HttpClient} from '@angular/common/http';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map'

@Injectable()
export class AuthenticationService {
    public token: string;
    public deviceList: any = [];
    constructor(private httpClient: HttpClient) {
        // set token if saved in local storage
        var currentUser = JSON.parse(localStorage.getItem('currentUser'));
        this.token = currentUser && currentUser.token;
    }

    login(username: string, password: string): Observable<boolean> {
        return this.httpClient.post('http://58.187.109.83:8001/api/user/login', { username: username, password: password })
            .map(response => {
                if(response['err'] == 1)
                {
                    console.log(response['err']);
                }
                else
                {
                    // login successful if there's a jwt token in the response
                    console.log(response);
                    // this.deviceList = response['dev'];
                    // console.log(this.deviceList);
                    let token = response['token'];
                    if (token) {
                        // set token property
                        this.token = token;

                        // store username and jwt token in local storage to keep user logged in between page refreshes
                        localStorage.setItem('currentUser', JSON.stringify({ token: token }));

                        // return true to indicate successful login
                        return true;
                    } else {
                        // return false to indicate failed login
                        return false;
                    }              
                }           
            });
    }

    register(username: string, password: string): Observable<boolean> {
        return this.httpClient.post('http://113.22.46.193:8001/api/user/register', { username: username, password: password })
            .map(response => {
                if(response['err'] == 1)
                {
                    console.log(response);
                }
                else
                {
                    // login successful if there's a jwt token in the response
                    console.log(response);
                    // this.deviceList = response['dev'];
                    // console.log(this.deviceList);
                    let token = response['token'];
                    if (token) {
                        // set token property
                        this.token = token;

                        // store username and jwt token in local storage to keep user logged in between page refreshes
                        localStorage.setItem('currentUser', JSON.stringify({ token: token }));

                        // return true to indicate successful login
                        return true;
                    } else {
                        // return false to indicate failed login
                        return false;
                    }              
                }           
            });
    }

    logout(): void {
        // clear token remove user from local storage to log user out
        this.token = null;
        localStorage.removeItem('currentUser');
    }
}


